<script>
import guideComponentProperty from './guideComponentProperty'
//
//   
// select   select 
export default {
  render(h, cxt) {
    try {
      return h(require(`./${this.value.controlType}.vue`).default, {
        props: {
          value: this.value,
          form: this.form,
          mode: this.mode,
        },
      })
    } catch (error) {
      throw new Error(
        '' + this.value.controlType + ''
      )
    }
  },
  mixins: [guideComponentProperty],
  methods: {},
  created() {},
}
</script>

