<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6" class="occupancy">
        <affix>
          <el-card class="box-card">
            <div slot="header" class="flexBox">
              <div></div>
            </div>
            <draggable
              class="dragArea list-group"
              :list="contentList"
              :group="{ name: 'people', pull: 'clone', put: false }"
              :clone="cloneDog"
              :move="checkMove"
            >
              <div
                class="list-group-item contentList"
                v-for="(item,index) in contentList"
                :key="index"
              >
                <el-button class="contentButton">{{item.label}}</el-button>
              </div>
            </draggable>
          </el-card>
        </affix>
      </el-col>

      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="flexBox">
            <div class="flex1"></div>
            <div v-if="data && data.length>0">
              <el-button size="mini" @click="printVisible = true"></el-button>
              <el-button size="mini" @click="testVisible = true"></el-button>
              <el-button size="mini" @click="visible = true"></el-button>
            </div>
          </div>
          <el-form id="customerForm" ref="form" label-width="150px" class="customerForm designForm">
            <draggable
              v-bind="dragOptions"
              class="dragArea list-group"
              :list="data"
              group="people"
              tag="transition-group"
              id="content"
            >
              <div
                class="list-group-item flexBox myField"
                @click="getActivie(item)"
                :class="{'activie':item.activie,}"
                :mycol="item.row"
                v-for="(item,index) in data"
                :key="index"
              >
                <guideComponent
                  class="flex1"
                  :mode="0"
                  :class="{'designFormRequired':item.required,}"
                  :value="item"
                ></guideComponent>
                <div class="deleteIconDiv">
                  <i class="el-icon-circle-close deleteIcon" @click="deleteRow(item)"></i>
                </div>
              </div>
            </draggable>
          </el-form>
        </el-card>
      </el-col>

      <el-col :span="6">
        <affix>
          <el-card class="box-card">
            <div slot="header" class="flexBox">
              <div class="flex1"></div>
              <div class="flexContent" v-if="current">{{current.name}}</div>
            </div>

            <el-form
              ref="attributeForm"
              v-if="current && current.component"
              :model="current.component"
              label-width="100px"
            >
              <el-form-item label="">
                <el-select
                  class="width100"
                  v-model="current.component.row"
                  placeholder=""
                  readonly
                >
                  <el-option
                    v-for="(item, i) in rowSelect"
                    :key="i"
                    :label="item.label "
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="">
                <el-input
                  v-model="current.component.label"
                  :placeholder="current.component.placeholder"
                ></el-input>
              </el-form-item>

              <el-form-item
                label=""
                v-if="current.component.controlType == 'text'||current.component.controlType == 'textarea'"
              >
                <el-input v-model="current.component.placeholder" placeholder=""></el-input>
              </el-form-item>

              <template
                label=""
                v-if="current.component.controlType == 'radio' || current.component.controlType=='select' || current.component.controlType == 'checkbox'"
              >
                <el-form-item
                  :label="''+(index+1)"
                  v-for="(box, index) in current.component.enum"
                  :key="index"
                >
                  <div class="flexBox">
                    <div class="flex1">
                      <el-input v-model="box.label" placeholder=""></el-input>
                    </div>
                    <div class="flexContent rowIcon" @click="addEnum(current.component)">
                      <i class="el-icon-plus pointer"></i>
                    </div>
                    <div
                      class="flexContent rowIcon"
                      v-if="current.component.enum.length>1"
                      @click="removeEnum(current.component,index)"
                    >
                      <i class="el-icon-minus pointer"></i>
                    </div>
                  </div>
                </el-form-item>
              </template>

              <template v-if="current.component.controlType == 'inputNumber'">
                <el-form-item label="">
                  <el-input-number
                    v-model="current.component.nzMin"
                    placeholder=""
                    :min="-2147483648"
                    :max="2147483647"
                    @change="numberChange(current.component)"
                  ></el-input-number>
                </el-form-item>
                <el-form-item label="">
                  <el-input-number
                    v-model="current.component.nzMax"
                    placeholder=""
                    :min="-2147483648"
                    :max="2147483647"
                    @change="numberChange(current.component)"
                  ></el-input-number>
                </el-form-item>
              </template>

              <template v-if="current.component.controlType == 'text'">
                <el-form-item label="">
                  <el-select
                    class="width100"
                    v-model="current.component.regular"
                    placeholder=""
                    readonly
                  >
                    <el-option
                      v-for="(item, i) in formExpression"
                      :key="i"
                      :label="item.label "
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </template>

              <template
                v-if="current.component.controlType != 'radio' && current.component.controlType != 'partitionLine' "
              >
                <el-form-item :label="current.component.controlType == 'checkbox'?'':''">
                  <el-switch v-model="current.component.required"></el-switch>
                </el-form-item>
              </template>

              <el-form-item label="">
                <el-switch v-model="current.component.disabled"></el-switch>
              </el-form-item>
            </el-form>
          </el-card>
        </affix>
      </el-col>
    </el-row>
    <el-dialog
      title=""
      :visible="visible"
      v-if="visible"
      width="900px"
      append-to-body
      custom-class="myDialog"
      :destroy-on-close="true"
      @close="visible = false"
      :close-on-click-modal="false"
    >
      <customerForm :data="data" :preview="true"></customerForm>
      <div slot="footer">
        <el-button @click=" visible = false"></el-button>
        <el-button type="primary" @click=" visible = false"></el-button>
      </div>
    </el-dialog>

    <el-dialog
      title=""
      :visible="testVisible"
      v-if="testVisible"
      width="900px"
      append-to-body
      custom-class="myDialog"
      :destroy-on-close="true"
      @close="testVisible = false"
      :close-on-click-modal="false"
    >
      <customerForm :data="data" ref="testForm"></customerForm>
      <div slot="footer">
        <el-button @click=" testVisible = false"></el-button>
        <el-button type="primary" @click="testSave"></el-button>
      </div>
    </el-dialog>

    <el-dialog
      title=""
      :visible="printVisible"
      v-if="printVisible"
      width="900px"
      append-to-body
      custom-class="myDialog"
      :destroy-on-close="true"
      @close="printVisible = false"
      :close-on-click-modal="false"
    >
      <el-form id="printForm" label-width="100px" class="customerForm">
        <guideComponent
          class="myField"
          v-for="(item,index) in data"
          :key="index"
          :mode="2"
          :value="item"
        ></guideComponent>
      </el-form>
      <div slot="footer">
        <el-button @click=" printVisible = false"></el-button>
        <el-button type="primary" v-print="printObj"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { checkArrayString, checkArray, uuid } from '@/utils/index'
import draggable from 'vuedraggable'
import guideComponent from '@/components/customerForm/guideComponent/index'
import customerForm from '@/components/customerForm/index'
export default {
  name: 'formIndex',
  components: {
    guideComponent,
    draggable,
    customerForm,
  },
  data() {
    return {
      //
      printObj: {
        id: 'printForm',
        extraCss: '/css/print.css',
        popTitle: '',
        extraHead: '<meta http-equiv="Content-Language"content="zh-cn"/>',
      },

      contentList: [
        {
          controlType: 'text',
          label: '',
        },
        {
          controlType: 'textarea',
          label: '',
        },
        {
          controlType: 'ueditor',
          label: '',
        },
        {
          controlType: 'select',
          label: '',
        },
        {
          controlType: 'datePicker',
          label: '',
        },
        {
          controlType: 'inputNumber',
          label: '',
        },
        {
          controlType: 'radio',
          label: '',
        },
        {
          controlType: 'checkbox',
          label: '',
        },
        {
          controlType: 'partitionLine',
          label: '',
        },
        {
          controlType: 'cityCountyAndDistrict',
          label: '',
        },
      ],
      data: [],
      drag: false,
      draggableDisabled: false,
      //
      current: {
        component: null,
        name: '',
      },

      //
      rowSelect: [
        { label: '1', value: '1' },
        { label: '1/2', value: '2' },
        { label: '1/3', value: '3' },
        { label: '1/4', value: '4' },
      ],

      //    
      formExpression: this.$PublicEnumeration.formExpression,

      visible: false,

      testVisible: false,

      printVisible: false,

      //  A1 A2 A3
      fieldESLength: 0,
    }
  },
  created() {},

  mounted() {},
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: 'description',
        disabled: false,
        ghostClass: 'ghost',
      }
    },
  },
  methods: {
    checkMove(e) {
      return e.to.id === 'content'
    },
    cloneDog(value) {
      this.fieldESLength++
      const row = {
        label: value.label,
        fieldES: 'A' + this.fieldESLength,
        controlType: value.controlType,
        value: null,
        required: true,
        disabled: true,
        placeholder: null,
        nzMax: null,
        nzMin: null,
        regular: null,
        enum: [],
        row: '1',
      }

      const type = value.controlType
      //end
      if (type == 'radio' || type == 'select') {
        for (let i = 1; i < 4; i++) {
          const sourceDatas = {
            label: '' + i,
            value: '' + i,
          }
          row.enum.push(sourceDatas)
        }
        //
        row.value = row.enum[0].value
      } else if (type == 'checkbox') {
        for (let i = 1; i < 4; i++) {
          row.enum.push({
            label: '' + i,
            value: false,
          })
        }
      } else if (type == 'partitionLine') {
        row.value = 'left'
        row.label = ''
      }
      this.getActivie(row)
      return row
    },
    //
    deleteRow(value) {
      this.data.splice(checkArrayString(this.data, 'fieldES', value.fieldES), 1)
    },
    //
    getActivie(value) {
      this.data.forEach((item) => {
        item.activie = false
      })
      value.activie = true
      this.current.name = this.contentList[
        checkArrayString(this.contentList, 'controlType', value.controlType)
      ].label
      this.current.component = value
    },

    // checkbox select radio 
    addEnum(e) {
      let row = {
        label: '' + (e.enum.length + 1),
        value:
          e.controlType == 'checkbox' ? false : '' + (e.enum.length + 1),
      }
      e.enum.push(row)
    },

    //  checkbox select radio 
    removeEnum(e, index) {
      const value = e.enum[index].value
      e.enum.splice(index, 1)
      e.value = e.value == value ? e.enum[0].value : e.value
    },

    //
    numberChange(item) {
      item.nzMin = item.nzMin >= item.nzMax ? item.nzMax - 1 : item.nzMin
      item.value = item.value < item.nzMin ? item.nzMin : item.value
      item.value = item.value > item.nzMax ? item.nzMax : item.value
    },

    testSave() {
      this.$refs.testForm.$refs.ruleForm.validate((valid) => {
        if (valid) {
          alert('')
        } else {
        }
      })
    },

    testFn() {},
  },
}
</script>
<style scoped lang="scss">
.list-group {
  margin-block-start: 0;
  margin-block-end: 0;
  padding-inline-start: 0;
  li {
    list-style: none;
  }
}

.contentButton {
  width: 100%;
  margin-bottom: 10px;
  display: block;
  margin-left: 0;
}

.contentBox:before,
.contentBox:after {
  display: table;
  content: '';
}

.contentBox:after {
  clear: both;
}

.gu-unselectable {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
  background-color: #fff;
}

.gu-transit {
  opacity: 0.2;
  padding: 10px 0;
  border: 2px dashed;
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 0.4s ease-in-out;
  -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=20)';
  filter: alpha(opacity=20);
}

.gu-mirror {
  padding: 10px 0;
  box-sizing: content-box;
  background-color: rgba(0, 0, 0, 0.3);
  border: 2px dashed;
  transition: opacity 0.4s ease-in-out;
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.judgment {
  display: none !important;
}

.el-row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}
#content {
  padding-bottom: 80px;
}

#contentList,
#content {
  min-height: calc(100vh - 200px);
  overflow: auto;
  display: block;
  overflow: hidden;
}

.customer-form .noLabel .el-form-item__label {
  display: none;
}

.customer-form .mce-panel {
  width: calc(100% - 2px) !important;
}

.conten {
  overflow: hidden;
}

.activie {
  padding: 3px;
  background: rgba(0, 0, 0, 0.2);
  .el-divider--horizontal {
    // .el-divider__text {
    background: rgba(0, 0, 0, 0.2);
    // }
  }
}
.rowIcon {
  cursor: pointer;
  width: 30px;
}

.designForm {
  .el-form-item {
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
    float: left;
    display: flex;
  }
  .deleteIconDiv {
    padding: 10px;
    .deleteIcon {
      font-size: 22px;
      cursor: pointer;
    }
  }
}
</style>
